
import React, {useRef, useEffect } from 'react';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';


const NftBanner = (width, height)=>{
  const renderer = new THREE.WebGLRenderer({antialias:true, alpha:true});
  renderer.setSize(width, height);
  renderer.outputEncoding = THREE.sRGBEncoding;
  renderer.physicallyCorrectLights = true;

  const camera = new THREE.PerspectiveCamera( 60, width / height, 1, 500 );
  camera.position.set( 50, 0, 10 );
  camera.lookAt(0,0,0);
  const scene = new THREE.Scene();
  
  const URL_PREFIX = process.env.PUBLIC_URL;  

  let postcard1:THREE.Object3D;
  let postcard2:THREE.Object3D;

  const init=()=>{
    addPostcard();
    addLight();
  }

  const addPostcard = () => {
    const loader = new GLTFLoader();

    loader.load(`${URL_PREFIX}/nft/peace_maker_51.glb`, (gltf)=>{
      gltf.scene.scale.multiplyScalar(2);
      gltf.scene.position.y = 9;
      gltf.scene.rotation.y = Math.PI/2;
      postcard1 = gltf.scene;
      scene.add(gltf.scene);
    });

    loader.load(`${URL_PREFIX}/nft/peace_maker_1.glb`, (gltf)=>{
      gltf.scene.scale.multiplyScalar(2);
      gltf.scene.position.y = -9;
      postcard2 = gltf.scene;
      scene.add(gltf.scene);
    });
  }

  const addLight = () => {
    const light1  = new THREE.AmbientLight( 0xffffff, 2);
    const light2  = new THREE.DirectionalLight(0xffffff, 2);
    light2.position.set(0.5, 0, 0.866); // ~60
    scene.add(light1);
    scene.add(light2);
  }

  const render = () =>{
    renderer.render(scene, camera);

    if(postcard1){
      postcard1.rotation.y += 0.01;
    }
    if(postcard2){
      postcard2.rotation.y -= 0.01;
    }
  }

  const animate = () => {
    render();
  }

  const getRenderer=()=>{
    return renderer;
  }

  return {init, getRenderer, animate}
}

const NftView = () => {
  const ref:any = useRef();

  useEffect(() => {
    if(!ref.current){
      return;
    }

    const width = ref.current.offsetWidth;
    const height = 900;
    const metaBanner = NftBanner(width, height);
    metaBanner.init();
    ref.current.appendChild(metaBanner.getRenderer().domElement);

    let animationNumber;
    const animate = () => {
      animationNumber = requestAnimationFrame( animate );
      metaBanner.animate();
    }

    animate();

    return () => {
        // Callback to cleanup three js, cancel animationFrame, etc
        if(ref.current){
          ref.current.innerHTML = '';
        }
        cancelAnimationFrame(animationNumber);
    }
  }, []);

  return <div ref={ref}/>;
}

export default NftView;
