import * as React from 'react';
import Container from '@mui/material/Container';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Button from '../components/Button';
import Stack from '@mui/material/Stack';
import { BASE_URL } from 'modules/utils/Constants';
import Typography from 'modules/components/Typography';

interface ClaimedAddress {
	address: string
	status: string
  txn: string
  nft: string
  createdAt: Date
}

async function getList(address:string): Promise<ClaimedAddress[]> {
  let url = BASE_URL + 'list?'
  if (address != undefined && address != '') {
    url += 'address=' + address + '&';
  }
  url += 'list?take=10&skip=0';
  const res = await fetch(url);
  const response = await res.json();
  return (response['data'] as ClaimedAddress[]);
}

interface ClaimedNFTProps {
  address:string,
}

export default function ClaimedNFT(props: ClaimedNFTProps) {
  const [rows, setRows] = React.useState([]);
  const [address, setAddress] = React.useState(props.address);
  const [dataloaded, setDataLoaded] = React.useState(false);

  React.useEffect(() => {
    if (!dataloaded) {
      getList(address).then(rows => {
        setRows(rows);
        setDataLoaded(true);
      })
    }
  })
  return (
    <Container
        sx={{
          mt: 3,
          mb: 14,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        }}
      >
    {getHeader(address)}
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell align="center">Address</TableCell>
            <TableCell align="center">Claimed At</TableCell>
            <TableCell align="center">Status</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.address}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row" align="center">
                {row.address}
              </TableCell>
              <TableCell align="center">{row.createdAt}</TableCell>
              <TableCell align="center">
                {getStatusCell(row.status, row.nft, row.txn)}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </Container>
  );
}

function getHeader(address: string) {
  if (address == undefined || address == '') {
    return (
      <Typography
        color="black"
        align="center"
        variant="subtitle1"
        sx={{ 
          mb: 4, mt: { sx: 5, sm: 5 } }}
      >
        OVERALL CLAIM LIST
      </Typography>
    );
  }
  else {
    return (
      <Typography
        color="black"
        align="center"
        variant="subtitle1"
        sx={{ 
          mb: 4, mt: { sx: 5, sm: 5 } }}
      >
        YOUR CLAIM LIST
      </Typography>
    );
  }
}

function getStatusCell(status: string, nft: string | undefined, txn: string | undefined) {
  if (status == "claimed") {
    return `In Queue`
  }
  else if (status == "processed") {
    return (<Button color="success"
    variant="contained"
    size="medium"
    component="button"
    onClick={() => {window.open(txn, "_blank")}}
    sx={{ maxWidth: 60, borderRadius:2, maxHeight:30 }}> TXN </Button>)
  }
  else if (status == "granted") {
    return (
      <Stack direction="row" spacing={1} alignItems="center" justifyContent="center">
        <Button color="success"
          variant="contained"
          size="medium"
          component="button"
          onClick={() => {window.open(nft, "_blank")}}
          sx={{ maxWidth: 60, borderRadius:2, maxHeight:30 }}> 
          NFT
      </Button>
      <Button color="success"
          variant="contained"
          size="medium"
          component="button"
          onClick={() => {window.open(txn, "_blank")}}
          sx={{ maxWidth: 60, borderRadius:2, maxHeight:30 }}> 
          TXN 
      </Button>
      </Stack>
    )
  }
}
