import * as React from 'react';
import Button from '../components/Button';
import Typography from '../components/Typography';
import ProductHeroLayout from './ProductHeroLayout';
import { TextField } from '@mui/material';
import Image from '../../assets/flag.png';
import {validateAddress} from '../utils/AddressValidator';
import Stack from '@mui/material/Stack';
import { alpha, styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import { BASE_URL } from 'modules/utils/Constants';

const WhiteTextField = styled(TextField)({
  '& label.Mui-focused': {
    color: 'white',
  },
  '& .MuiInput-underline:after': {
    borderBottomColor: 'white',
  },
  '& .MuiOutlinedInput-root': {
    '& fieldset': {
      borderColor: 'white',
    },
    '&:hover fieldset': {
      borderColor: 'yellow',
    },
    '&.Mui-focused fieldset': {
      borderColor: 'white',
    },
  },

  input: {
    '&::placeholder': {
      color: 'white',
    },
 
}
});

export default function ProductHero() {
  const [address, setAddress] = React.useState("");
  const [error, setError] = React.useState(false);
  const [helperText, setHelperText] = React.useState("");

  async function claimAddress(address: string): Promise<String> {
    const res = await fetch(BASE_URL + 'request/' + address, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
    });
    const response = await res.json();
    if (response['error']) {
      throw response['error'];
    }
    else {
      return (response as String);
    }
  }

  function onSubmit() {
    if (validateAddress(address)) {
      claimAddress(address)
      .then(res => {
        location.href='#/list/' + address
      })
      .catch(e => {
        setError(true);
        setHelperText(e);
      })
      
    }
    else {
      setError(true);
      setHelperText("Crypto address is invalid. Please check again");
    }
  }
  

  return (
    <ProductHeroLayout
      sxBackground={{
        backgroundImage: `url(${Image})`,
        backgroundColor: 'common.white',
        backgroundPosition: 'center',
      }}
    >
      <Typography color="white" align="center" variant="h2" marked="center"
      
      sx={{mt:10}}>
        STOP THE WAR
      </Typography>
      <Typography
        color="white"
        align="center"
        variant="subtitle1"
        sx={{ 
          mb: 4, mt: { sx: 5, sm: 5 } }}
      >
        We are sending a ‘Thank You For Your Help’ token to all those who have contributed crypto funds to Ukraine’s war relief funds to help the people impacted by the ongoing war. This token is sent as a series of NFTs depicting some of the famous places of Ukraine in a digital postcard.
      </Typography>

    
      <Stack direction="column" spacing={1}
        sx={{
          display: 'flex',
          width: '100%',
          justifyContent: 'center',
          alignItems: 'center'
      }} >

      <Grid item xs={12} md={12} sx={{zIndex:99}}>
        <WhiteTextField

          id="address"
          required
          error={error}
          inputProps={{ style: { color: "#ffffff" } }}
          size="medium"
          variant="outlined"
          label = "Enter you crypto address here"
          InputLabelProps={{
            style: { color: '#cccccc' }, 
         }}
          onChange={(e) => setAddress(e.target.value)}
          helperText={helperText}
        />

        </Grid>
        <Grid item xs={12} md={12} sx={{zIndex:99}}> 
          <Button
    
            color="secondary"
            variant="contained"
            size="medium"
            component="button"
            onClick={() => onSubmit()}
            sx={{ borderRadius:2 }}
          >
            Claim
          </Button>
          </Grid>
      </Stack>
    </ProductHeroLayout>
  );
}
