import * as React from 'react';
import Container from '@mui/material/Container';
import Typography from '../components/Typography';

import Grid from '@mui/material/Grid';
import Image from '../../assets/tweet.png';
import { BALANCES_URL } from 'modules/utils/Constants';

interface Balance {
  type: string
  value: string
}

async function getBalance(url: string): Promise<Balance[]> {
  const res = await fetch(url);
  const response = await res.json();
  return (response as Balance[]);
}

export default function JoinHands() {
  const [eth, setEth] = React.useState("0.0");
  const [usdt, setUsdt] = React.useState("0.0");
  const [dataloaded, setDataLoaded] = React.useState(false);

  React.useEffect(() => {
    if (!dataloaded) {
      getBalance(BALANCES_URL).then(res => {
        res.forEach(r => {
          if (r.type == 'eth_balance') {
            setEth(r.value);
          }
          else {
            setUsdt(r.value);
          }
        });
      });
    }
  })
  return (
    <Container component="section" sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center',mt: 12, mb: 12 }}>
      <Typography 
        variant="h4" 
        marked="center" 
        align="center" 
        component="h2"
      >
        Join Hands Together
      </Typography>
      
      <Typography 
     variant="subtitle1"  
     component="div" 
     align="center" 
     sx={{ mt: 4}}
      >
        Ukraine Crypto Donation at <b>0x165CD37b4C644C2921454429E7F9358d18A45e14</b>
      </Typography>
      <Typography 
        variant="subtitle1"  
        component="div" 
        align="center" 
        sx={{ mt: 2, mb: 4 }}
      > 
        This is the public address for Ethereum and USDT donation for Ukraine war relief funds.
      </Typography>
      <Grid 
        container 
        spacing={2} 
        direction="column"
        justifyContent="center"
        alignItems="center">
        <Grid item xs={6}>
          <Typography 
            variant="subtitle3"  
            component="div" 
            align="center" 
            sx={{ mt: 1, mb: 1 }}
          > 
            <b>{eth} Eth</b> donated till date.
          </Typography>
          <Typography 
            variant="subtitle3"  
            component="div" 
            align="center" 
            sx={{ mt: 1, mb: 1 }}
          > 
            <b>{usdt} USDT</b> donated till date.
          </Typography>
          {/* <Typography 
            variant="subtitle3"  
            component="div" 
            align="center" 
            sx={{ mt: 1, mb: 1 }}
          > 
          <b>1,785.9912 People</b> have donated yesterday.
          </Typography> */}
        </Grid>
        <Grid item xs={4}>
          <a href="https://twitter.com/Ukraine/status/1497594592438497282" target="_blank">
            <img 
              style={{
                maxWidth:'420px',
                width:'100%',
                height:'auto'
              }}
              src={Image} />
          </a>
        </Grid>
      </Grid>
    </Container>
  );
}
